@media screen and (max-width:800px) {
  .nav {
    display: none;
  }
}

.nav__list {
  display: flex;
  justify-content: space-between;
  padding-top: 5%;
  @media screen and (min-width:1900px) {
    margin: -3% 10%;
  }
  
  .nav__link {
    color: #fff;
    font-size: 12px;
    &:hover {
      color: #9fadc2;
    }

    &--active {
      padding: 8px 24px;
      border-radius: 8px;
      color: #fff;
      background-color: rgba( 255, 255, 255, 0.1);
    }
  }
}
